.home{
    display: flex;
    flex-direction: column; 
    justify-content: center;
}

.section{
    
    width: 100%;
    margin-bottom: 20px;
    height: 120vh;
   
}

.home__sectionImage__one{
    background: url(/images/home__image1.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    width: 100%;
 
 
}

.sectionOne__text{
    display: flex;
    flex-direction: column;
    position: relative;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    top: 70px;
    margin-bottom: 50px;

   
}

.sectionOne__text > h1{
    color:  #ff0000 ;
    -webkit-text-stroke: 1px black;
    font-size: 2em;
   
  
}

.sectionOne__text > h2{
    text-align: justify;
    color: #000000;
    font-weight: 500;   
    padding: 13.5px;
    font-size: 3.5rem;
}



.sectionOne__text > .listadoCiudades{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    text-align: left;
    

}

.sectionOne__text > .listadoCiudades > ul{
    display: inline-block;
    min-width: 500px;
    list-style: none;
    padding: 10px;
    text-align: left;
    font-size: 1.5rem;

    

}

.sectionOne__text >.listadoCiudades > ul > li > h2{
    font-size: 1.5rem;
    font-weight: bolder;
    text-align: left;
     color: #fff ; 
    -webkit-text-stroke: 0.5px black;

}

.sectionOne__text  .logo-name{
    -webkit-text-stroke: 1px black;
    color: #01b400;
    align-items: center;
    
}


.services__container{
    background: linear-gradient(to right, #ff0000 , #ab0000);
    border-radius: 25px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    
}



.home__sectionImage__two{
    background: url(/images/home__image2.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    align-items: flex-end;
    justify-content: flex-end;
    display: flex;
 
}

.sectionTwo__text{
    
    position: relative;
    width: 100%;
}

.sectionTwo__text:before{
    
    width: 200px;
    height: 10px;
    color: red;
    position: absolute;
    top: 50%;
}


.home__sectionImage__three{
    background: url(/images/home__image3.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    
 
}




@media (min-width: 980px){

   
    .sectionOne__text > p > a{
        font-size: xx-large;
        text-align: center;
    }

   
    
}
@media (max-width: 980px){

   
    .section{
        height: auto;
    }

   
    
}

@media (max-width: 768px){

    .section{
        height: auto;
    }


    .home__sectionImage__one{
        
        background-position-x: 77%;
      
    }
   
    .sectionOne__text{
        width: 100%;
        justify-content: flex-start;   
        text-align: left;
        margin-bottom: 100px;
        padding: 0px;
    }

    .sectionOne__text > .listadoCiudades > ul{
        min-width: 40%;
          
    }
    .sectionOne__text > .listadoCiudades > ul > li > a{
        font-size: large;
          
    }
  

    .sectionOne__text >h1{
        font-size: 1.5em;
        text-align: center;
    
    }

    .sectionOne__text > h2{
        text-align: left;
        font-size: 3rem;
        padding: 10px;
    }

    .home__sectionImage__two{
        background-position-y: 60px;
    }

    .sectionTwo__text{
        width: 100%;
        height: 150vh;
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .service__card{
        background: linear-gradient(to right, #ff0000 , #ab0000);

    }
}